import React, { Component } from 'react';
import './style.css';
import Focus1 from './focus1';
import Focus2 from './focus2';
import Focus3 from './focus3';


class Focus_more extends Component {
    scrollT() {
        window.scrollTo(0, 0);
      }


    render(){
    return (
       
        <header>
 {this.scrollT()}
<div className="row">
            <div className="col-lg-12 children_more_h1">
            <h1>Структура роботи</h1>
                    <h5>3 гуртки, 19 груп, які працюють за програмами: </h5>
                    </div>
                </div>

<main>
  
  <input id="tab1" type="radio" name="tabs"  className="children_more_input" />
  <label htmlFor="tab1"><i className="fas fa-capsules child1_more"></i>
            {/* <h4>4 - 7 years</h4> */}
            </label>
    
  <input id="tab2" type="radio" name="tabs" className="children_more_input" defaultChecked/>
  <label htmlFor="tab2"><i className="fab fa-earlybirds child2_more"></i>
            {/* <h4>8 - 12 years</h4> */}
            </label>
    
  <input id="tab3" type="radio" name="tabs" className="children_more_input"/>
  <label htmlFor="tab3"><i className="fas fa-award child3_more"></i>
            {/* <h4>13 - 99 years</h4> */}
            </label>
    

    
  <section id="content1" className="children_more_section">
    <Focus1 />
  </section>
    
  <section id="content2" className="children_more_section">
  <Focus2 />
  </section>
    
  <section id="content3" className="children_more_section">
  <Focus3 />
  </section>
    
    
</main>

        </header>
            )
        }
    }
        
export default Focus_more;