import React from 'react';
import './style.css';


function Subscribe() {
    return (
        <section>
            <div className="container-fluid" id="subscribe">
                <div className="row">
                    <div className="col-lg-12 subscribe">
                        <h1>Subscribe to Newsletter</h1>

                    </div>
                </div>
                <div className="row justify-content-center subscribe_form">
                    <div className="col-lg-4 col-md-4 col-sm-4 subscribe_input">
                        <input type="email" placeholder="Enter your emeil" id="subscribe_input"/>

                    </div>

                    <div className="col-lg-4 col-md-4 col-sm-4 subscribe_batton">

                        <input type="button" value="SUBSCRIBE NOW" id="subscribe_batton" />
                    </div>

                </div>
            </div>
        </section>
    )
}

export default Subscribe;