import React, { Component } from 'react';
import './style.css';
import Alboms_more_item from './allboms_item';

import PreviousNextMethods from './allboms_item/slyder';




class Alboms extends Component {
    constructor(props) {
        super(props);
    }

    state = {
        foto_more_cover: [],
        foto_more_discription:[]
    };

    createFotoMoreCover = () => {
        const { foto_more_cover } = this.state;

        if (foto_more_cover.length) {
            return foto_more_cover.map(cover_more => <Alboms_more_item key={cover_more.id} cover_more={cover_more} />)
        }

        return <div>no foto</div>



    };

    createFotoMoreCoverS = () => {
        const { foto_more_cover } = this.state;

        console.log({foto_more_cover})

        if (foto_more_cover.length) {
            return  <PreviousNextMethods key={foto_more_cover.id} cover_moreS={foto_more_cover} />
        }

        return <div>no foto</div>


    };




    createFotoMoreDiscription = () => {
        const {foto_more_discription} = this.state;

        console.log({foto_more_discription})

        if (foto_more_discription.length) {
            return (
            <div className="col-lg-12 portfolio_more_opis">
            <h1>{foto_more_discription[0].name}</h1>
            <p>{foto_more_discription[0].opis}</p>
            </div>
       )
     }

        return <div>no foto</div>


    };


    render() {
       

        return (
            <div>

                 <div className="row">
                     
                            {this.createFotoMoreDiscription()}
                      
                    </div>
                <div>
                    {this.createFotoMoreCoverS()}
                </div>
                <div className="container" id="alboms_more">
                    <div className="row portfolio_row main">
                        {this.createFotoMoreCover()}
                        
                    </div>
                </div>
            </div >
        )
    }

    componentDidMount() {

        const { id } = this.props.match.params
        console.log(id)
        fetch(`/fenix/json_alboms/albom${id}.json`)

            .then(res => res.json())

            .then(res => {

                this.setState({

                    foto_more_cover: (res.albom),
                    foto_more_discription: (res.discription)
                })

            }
            )

    }
}

export default Alboms;