import React, { Component } from 'react';
import './style.css';
import Select1 from './select1';
import Select2 from './select2';
import Select3 from './select3';
import Select4 from './select4';
import Select5 from './select5';
import Select6 from './select6';


class Select_us extends Component {
  scrollT() {
      window.scrollTo(0, 0);
    }


  render(){
  return (
     
      <header>
{this.scrollT()}

<div className="row">
            <div className="col-lg-12 select_us_more_h1">
                    <h1>Why select us.</h1>
                    <h5>Package managed installs don’t include </h5>
                    </div>
                </div>

<main>
  
  <input id="tab1" type="radio" name="tabs"  className="select_us_more_input" defaultChecked/>
  <label htmlFor="tab1"> <i className="fas fa-door-open fa_1 select_us_fafa"></i>
            
            </label>
    
  <input id="tab2" type="radio" name="tabs" className="select_us_more_input"/>
  <label htmlFor="tab2"><i className="fas fa-person-booth fa_2 select_us_fafa"></i>
            
            </label>
    
  <input id="tab3" type="radio" name="tabs" className="select_us_more_input"/>
  <label htmlFor="tab3"><i className="fas fa-shower fa_3 select_us_fafa"></i>
          
            </label>

    <input id="tab4" type="radio" name="tabs"  className="select_us_more_input" />
  <label htmlFor="tab4"><i className="fas fa-couch fa_4 select_us_fafa"></i>
            
            </label>
    
  <input id="tab5" type="radio" name="tabs" className="select_us_more_input"/>
  <label htmlFor="tab5"><i className="fas fa-trophy fa_1 select_us_fafa"></i>
          
            </label>
    
  <input id="tab6" type="radio" name="tabs" className="select_us_more_input"/>
  <label htmlFor="tab6"><i className="fas fa-volleyball-ball fa_2 select_us_fafa"></i>
           
            </label> 
    

    
  <section id="content1" className="select_us_more_section">
    <Select1 />
  </section>
    
  <section id="content2" className="select_us_more_section">
  <Select2 />
  </section>
    
  <section id="content3" className="select_us_more_section">
  <Select3 />
  </section>
  <section id="content4" className="select_us_more_section">
    <Select4 />
  </section>
    
  <section id="content5" className="select_us_more_section">
  <Select5 />
  </section>
    
  <section id="content6" className="select_us_more_section">
  <Select6 />
  </section>
    
</main>

        </header>
            )
        }
      }
export default Select_us;