import React from 'react';
import './style.css';


function Team_description() {
    return (
        <section>
            <div className="container-fluid" id="team_description">
                <div className="row justify-content-between team_description block">
                    <div className="col-lg-8 ">
                      <h4>Anna Schröder</h4>
                      <p>Apparently we had reached a great height in the atmosphere, for the sky was a dead black, and the stars had ceased to twinkle. By the same illusion which lifts the horizon of the sea to the level of the spectator on a hillside</p>
                    

                    </div>
                    <div className="col-lg-3 team_fafa">
                      <ul>
                          <li><i className="fab fa-facebook"></i>Facebook</li>
                          <li><i className="fab fa-twitter-square"></i>Twitter</li>
                          <li><i className="fab fa-linkedin"></i>Linkedin</li>
                          <li><i className="fab fa-instagram"></i>Instagram</li>
                      </ul>

                    </div>
                </div>
            </div>
        </section>
    )
}

export default Team_description;