import React, { Component } from 'react';
import './style.css';
import Child1 from './child1';
import Child2 from './child2';
import Child3 from './child3';


class Children_more extends Component {
    scrollT() {
        window.scrollTo(0, 0);
      }


    render(){
    return (
       
        <header>
 {this.scrollT()}

<div className="row">
            <div className="col-lg-12 children_more_h1">
                    <h1>Our Children.</h1>
                    <h5>Package managed installs don’t include </h5>
                    </div>
                </div>

<main>
  
  <input id="tab1" type="radio" name="tabs"  className="children_more_input" />
  <label htmlFor="tab1"><i className="fas fa-baby child1_more"></i>
            {/* <h4>4 - 7 years</h4> */}
            </label>
    
  <input id="tab2" type="radio" name="tabs" className="children_more_input" defaultChecked/>
  <label htmlFor="tab2"><i className="fas fa-child child2_more"></i>
            {/* <h4>8 - 12 years</h4> */}
            </label>
    
  <input id="tab3" type="radio" name="tabs" className="children_more_input"/>
  <label htmlFor="tab3"><i className="fas fa-male child3_more"></i>
            {/* <h4>13 - 99 years</h4> */}
            </label>
    

    
  <section id="content1" className="children_more_section">
    <Child1 />
  </section>
    
  <section id="content2" className="children_more_section">
  <Child2 />
  </section>
    
  <section id="content3" className="children_more_section">
  <Child3 />
  </section>
    
    
</main>

        </header>
            )
        }
    }
export default Children_more;