import React from 'react';
import './style.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

function Focus() {
       
    return (
    <section>
            <div className="container-fluid" id="focus">
            <div className="row">
            <div className="col-lg-12 focus">
                    <h1>Структура роботи</h1>
                    <h5>3 гуртки, 19 груп, які працюють за програмами: </h5>
                    </div>
                </div>
                <div className="row focus_item justify-content-center">
            <div className="col-lg-4 col-md-4 focus1">
            <i className="fas fa-capsules child1_more"></i>
            <h4>«Классичний танець»</h4>
            <p> Cистема виразних засобів хореографічного мистецтва, що базується на ретельній розробці різних груп рухів та позицій ніг, рук, корпусу й голови</p>
                    </div>
                    <div className="col-lg-4 col-md-4 focus2">
                    <i className="fab fa-earlybirds"></i>
            <h4>«Студія образотворчого мистецтва»</h4>
            <p>Головна мета навчання у студії – формування естетичних смаків, вміння розуміти та оцінювати мистецтво, красу та багатство рідної та світової культури.</p>
                    </div>
                    <div className="col-lg-4 col-md-4 focus3">
                    <i className="fas fa-award"></i>
            <h4>«Акробатика»</h4>
            <p>это разновидность гимнастики, включающая в себя упражнения на ловкость, гибкость, прыгучесть, силу и балансировку.</p>
                    </div>
                   <Link to="focus" >  <button type="button" className="btn focus_btn">Детальніше</button></Link>
         
                </div>
                   </div>
        </section>
        )
}

export default Focus;
