import React from 'react';


function Alboms_more_item(props) {
    const { image } = props.cover_more;

    function big() {
        let imgbig = document.getElementsByClassName('image');
        Array.from(imgbig).map((el) => {
            el.addEventListener("click", (e) => {
                e.target.classList.toggle('imgbig');
                console.log(e)
                
            });
        });
    }

    return (
        <div onClick={big} className="col-lg-4 col4 portfolio_item">

            <img src={image} className="image" />
        </div>
    )
}

export default Alboms_more_item;