import React, { Component } from "react";
import Slider from "react-slick";

export default class PreviousNextMethods extends Component {




  
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
   this.state = {
   
      display: false,
      // width: 600,
    
   image: props.cover_moreS
   }
     
  }
 
  getImages = () =>{

   return this.state.image.map(e => (
    <div key={e.id} className="itemSl">
    <img src={e.image}  />
     
      </div>)
   
)
  }

  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
     
    };








    
    return (



      
      <div>
        {/* <h2 className="h2Slyder">Previous and Next methods</h2> */}



         {/* <button
          className="button"
          onClick={() =>
            this.setState({
              width: this.state.width + 100
            })
          }
        >
          {" "}
          increase{" "}
        </button>
        <button
          className="button"
          onClick={() =>
            this.setState({
              width: this.state.width - 100
            })
          }
        >
          {" "}
          decrease{" "}
        </button> */}
        <button
          className="button btnSlyder"
          onClick={() =>
            this.setState({
              display: !this.state.display
            })
          }
        >
        
          {" "}
          {"открыть cлайдер"}
        </button>



 <div className="sliderOll"
          style={{
            // width: this.state.width + "px",
            display: this.state.display ? "block" : "none"
          }}
        >

        <Slider ref={c => (this.slider = c)} {...settings} className="slider">
          
        {this.getImages()}
         
         
         
        </Slider>
        <div style={{ textAlign: "center" }}>
          <button className="button prev" onClick={this.previous}>
          <i className="fas fa-chevron-left"></i>
          </button>
          <button className="button next" onClick={this.next}>
          <i className="fas fa-chevron-right"></i>
          </button>
          </div>
        </div>
      </div>
    );
  }
}