import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


function Portfolio_more_item(props) {
 
    const {image, name, id, rout} = props.cover;
 

    return (
      <div className="col-lg-3 portfolio_item">
                        <img src={image} className="image"/>
                        <div className="overlay">
                            <div className="portfolio_over text">
                            
                            <Link to =  {rout} > {name} </Link>
                            
                            </div>
                        </div>

                    </div>
 )
}

export default Portfolio_more_item;