import React, { Component } from 'react';
import './style.css';
import Home_portfolio_more_item from './portfolio_more_item/home_portfolio_item';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

class Home_portfolio_more extends Component {
    constructor(props){
        super(props);
    }

    state = {
        foto_cover: []
    };

createFotoCover = () => {
    const {foto_cover} = this.state;

    if(foto_cover.length){
        
       return foto_cover.splice(0,6).map (cover => <Home_portfolio_more_item key={cover.id} cover={cover}/>) 
    }

    return <div>no foto</div>

    
};

    render() {
        return (

            <header>
                <div className="container" id="portfolio">
                    <div className="row">
                        <div className="col-lg-12 portfolio">
                            <h1>Portfolio.</h1>
                            <h5>The spectacle before us was indeed sublime.</h5>
                        </div>
                    </div>


                    <div className="row portfolio_row">
                        {this.createFotoCover()}

                    </div>
                    <Link to="portfolio" >  <button type="button" className="btn portfolio_btn">Детальніше</button></Link>
                </div>

            </header >
        )
    }

    componentDidMount(){
        fetch("json.json")
        .then(res => res.json())
        .then(res =>{
           
           this.setState({
            foto_cover: res.info
            
           })


           }
        ) 
    }
}

export default Home_portfolio_more;