import React from 'react';
import './style.css';


function Touch() {
    return (
        <section>
            <div className="container-fluid" id="touch">
                <div className="row">
                    <div className="col-lg-12 touch">
                        <h1>Get in Touch</h1>
                        <h5>We understand your requirement and provide quality works.</h5>

                    </div>
                </div>
                <div className="row justify-content-center touch_form">
                    <div className="col-lg-6 touch_input">
                        <input type="name" placeholder="Enter your name" />

                    </div>
                    <div className="col-lg-6 touch_input">
                        <input type="email" placeholder="Enter your Emeil" />

                    </div>
                </div>

                 <div className="row justify-content-center touch_form">
                    <div className="col-lg-6 touch_input">
                        <input type="name" placeholder="Subject" />

                    </div>
                    <div className="col-lg-6 touch_input">
                        <input type="email" placeholder="Topic" />

                    </div>
                </div>


                <div className="row justify-content-center touch_form">
                    <div className="col-lg-12 touch_input">
                        <textarea type="text" placeholder="Your Message" className="area_input" />

                    </div>

                     <div className="col-lg-12">

                    <input type="button" value="SEND MESSAGE" id="touch_batton" />
                </div>
                    
                </div>


               


            </div>
        </section>
    )
}

export default Touch;