import React, { Component } from 'react';
import './style.css';
import Portfolio_more_item from './portfolio_more_item';


class Portfolio_more extends Component {
    constructor(props){
        super(props);
    }

    state = {
        foto_cover: []
    };

createFotoCover = () => {
    const {foto_cover} = this.state;

    if(foto_cover.length){
        
       return foto_cover.map (cover => <Portfolio_more_item key={cover.id} cover={cover}/>) 
    }

    return <div>no foto</div>

    
};

    render() {
        return (

            <header>
                <div className="container" id="portfolio_more">
                    <div className="row">
                        <div className="col-lg-12 portfolio_more_opis">
                            <h1>Portfolio.</h1>
                            <h5>The spectacle before us was indeed sublime.</h5>
                        </div>
                    </div>


                    <div className="row portfolio_row">
                        {this.createFotoCover()}

                    </div>

                </div>

            </header >
        )
    }

    componentDidMount(){
        fetch("json.json")
        .then(res => res.json())
        .then(res =>{
           
           this.setState({
            foto_cover: res.info
            
           })


           }
        ) 
    }
}

export default Portfolio_more;